<script>
    import ContentHeadTop from '../components/ContentHeadTop'
    export default {
        components: {
    		ContentHeadTop,
		},
		methods: {
            cancel() {
				this.goBack();
            },
            save(){

            },
            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            },
            
        },
        data () {
            return {
                hid,
                name,
                gender,
                age,
                height,
                weight,
                native_province,
                cid,
                comp_name,
                site_name,
                create_time,
                update_time,
                degree,
                description,
                experience,
                idNo,
                avator,
                img_list,
                is_valid,
                lanage_level,
                phone,
                point,
                refuse_reasion,
                sid,
                stars,
